
html {
  scroll-behavior: smooth;
}

body {
  font-family: "Helvetica Neue", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #fff;
  background-color: var(--base-100);
}

.container {
  max-width: 1400px;
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;
  margin: 0 auto;
}

.logo {
  display: flex;
  align-items: center;
  gap: 10px;
  font-family: var(--font-family-fourth);
  font-size: 32px;
  font-weight: 800;
  line-height: 120%;
  text-transform: uppercase;
  color: var(--base-content-100);
  letter-spacing: -0.04em;
}

.logo__image {
  width: 60px;
  height: 32px;
}

.logo sup {
  font-size: 20px;
  margin-top: -10px;
  margin-left: -8px;
}

.language-button {
  position: relative;
  width: 68px;
  height: 58px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--base-200);
  font-size: 14px;
  font-weight: 500;
  text-transform: uppercase;
  color: var(--base-content-100);
  border-radius: 20px;
  padding: 24px;
  transition: all 0.2s ease;
  font-family: var(--font-family-second);
  /* From https://css.glass */
  background: rgba(255, 255, 255, 0.1);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(20px);
  -webkit-backdrop-filter: blur(20px);
  outline: 1px solid rgba(255, 255, 255, 0.2);
}

.language-button:hover {
  opacity: 0.8;
}

.language-button:active {
  transform: scale(0.95);
}

.started {
  height: 200dvh;
  position: relative;
}

.section-title {
  font-size: 40px;
  font-weight: 500;
  line-height: 120%;
  color: var(--base-content-100);
}

@media (max-width: 1024px) {
  .container {
    padding: 0 10px;
  }
}

.form-control {
  width: 100%;
}

.form-error {
  font-family: var(--font-family-second);
  font-size: 14px;
  font-weight: 400;
  color: oklch(63.7% 0.237 25.331);
  margin-top: 10px;
}

@media (max-width: 1024px) {
  .services__navbar-inner {
    align-items: start;
  }

  .section-title {
    text-align: left;
  }

  .form-error {
    font-size: 12px;
  }
}

@media (max-width: 768px) {
  .logo span,
  .logo sup {
    display: none;
  }

  .logo__image {
    width: 83px;
    height: 44px;
  }
}
