html {
  scroll-behavior: smooth;
}

body {
  color: #fff;
  background-color: var(--base-100);
  font-family: Helvetica Neue, sans-serif;
  font-size: 16px;
  font-weight: 400;
}

.container {
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.logo {
  font-family: var(--font-family-fourth);
  text-transform: uppercase;
  color: var(--base-content-100);
  letter-spacing: -.04em;
  align-items: center;
  gap: 10px;
  font-size: 32px;
  font-weight: 800;
  line-height: 120%;
  display: flex;
}

.logo__image {
  width: 60px;
  height: 32px;
}

.logo sup {
  margin-top: -10px;
  margin-left: -8px;
  font-size: 20px;
}

.language-button {
  background-color: var(--base-200);
  text-transform: uppercase;
  width: 68px;
  height: 58px;
  color: var(--base-content-100);
  font-size: 14px;
  font-weight: 500;
  font-family: var(--font-family-second);
  -webkit-backdrop-filter: blur(20px);
  background: #ffffff1a;
  border-radius: 16px;
  outline: 1px solid #fff3;
  justify-content: center;
  align-items: center;
  padding: 24px;
  transition: all .2s;
  display: flex;
  position: relative;
  box-shadow: 0 4px 30px #0000001a;
}

.language-button:hover {
  opacity: .8;
}

.language-button:active {
  transform: scale(.95);
}

.started {
  height: 200dvh;
  position: relative;
}

.section-title {
  color: var(--base-content-100);
  font-size: 40px;
  font-weight: 500;
  line-height: 120%;
}

@media (width <= 1024px) {
  .container {
    padding: 0 10px;
  }
}

.form-control {
  width: 100%;
}

.form-error {
  font-family: var(--font-family-second);
  color: oklch(63.7% .237 25.331);
  margin-top: 10px;
  font-size: 14px;
  font-weight: 400;
}

@media (width <= 1024px) {
  .services__navbar-inner {
    align-items: start;
  }

  .section-title {
    text-align: left;
  }

  .form-error {
    font-size: 12px;
  }
}

@media (width <= 768px) {
  .logo span, .logo sup {
    display: none;
  }

  .logo__image {
    width: 83px;
    height: 44px;
  }
}
